body {
  margin: 0;
  /* font-family: "Raleway", sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#neumorphism {
  border-radius: 41px;
  background: linear-gradient(145deg, #ffffff, #e6e6e6);
  box-shadow: 41px 41px 82px #ababab, -41px -41px 82px #ffffff;
}

#card {
  width: 95%;
  margin-top: 5%;
  margin-bottom: 5%;
  border: 0;
}

#textCard {
  background: linear-gradient(145deg, #ffffff, #e6e6e6);
  box-shadow: 20px 20px 60px #d9d9d9, -20px -20px 60px #ffffff;
  width: 95%;
  margin-top: 5%;
  margin-bottom: 5%;
  border: 0;
}

h1 {
  transition: all 0.6s ease-out;

  margin-top: -5px;
  font-family: "Montserrat Alternates", sans-serif;
}
h1:hover {
  transform: translate(2px, 0);
}
#logo {
  color: #331391;
}
a:hover {
  text-decoration: none;
}
#navbar {
  font-family: "Montserrat", sans-serif;

  margin-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
  border-radius: 25px;
  background: linear-gradient(175deg, #ffffff, #e6e6e6);
  box-shadow: 9px 9px 19px #a1a1a1, -9px -9px 19px #ffffff;
}
#pro-navbar {
  font-family: "Montserrat", sans-serif;
  background-color: transparent;
}

.tab {
  position: relative;
}

.tab:before {
  content: "";
  position: absolute;
  width: 90%;
  height: 0;
  bottom: 10%;
  left: 5%;
  background-color: #331391;
  visibility: hidden;
  transition: all 0.2s ease-in-out;
}

.tab:hover:before {
  visibility: visible;
  height: 1px;
}

#water img {
  width: 100%;
}

.images-container {
  padding: 0;
}

h1 {
  color: white;
}

.psalm23-text {
  position: absolute;
  top: 15%;
  left: 3%;
  /* transform: translate(-50%, -50%); */
  color: white;
  /* width: 75%; */
  font-family: "Nanum Myeongjo", serif;
  font-size: 3.5vw;
  text-align: start;
}
.wave-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  width: 75%;
  font-family: "Crimson Text", serif;
  font-size: 3vw;
}

.inner:hover {
  opacity: 1;
}
.inner {
  opacity: 0.9;
  width: 100%;
}
a {
  /* opacity: 0; */
  /* background: rgba(0, 0, 0, 0.5); */
}
.footer {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: #616161;
}

.footer a {
  color: whitesmoke;
  font-family: "Calibre Regular", sans-serif;
  letter-spacing: 0.025em;
  line-height: 1.5;
  font-size: 13.6px;
  padding-bottom: 5px;
  padding-top: 5px;
  text-align: start;
  padding-left: 0;
}

.footer-section {
  text-transform: uppercase;
  letter-spacing: 0.3em;
  font-size: 0.65em;
  color: white;
  line-height: 1.5;
  font-family: "Calibre Regular", sans-serif;
  font-weight: 400;
  margin-bottom: 5px;
  text-align: start;
}

#psalm19-text {
  color: black;
  font-family: "Amiri", serif;
  text-align: start;
  margin: auto;
  font-size: 1.5vw;
  font-weight: 500;
}
.images-container {
  padding: 0;
}

#text-box {
  padding-left: 19%;
  padding-right: 19%;
  white-space: nowrap;
}

#psalm19-header {
  font-family: "Nanum Myeongjo", serif;
  text-align: start;
  font-size: 2vw;
  font-weight: 700;
}

.alone-text {
  position: absolute;
  top: 15%;
  left: 10%;
  color: #663116;
  font-family: "Amiri", serif;
  font-size: 1.75vw;
  text-align: start;
}
.images-container img {
  width: 100%;
}
html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
}

