#psalm19-text {
  color: black;
  font-family: "Amiri", serif;
  text-align: start;
  margin: auto;
  font-size: 1.5vw;
  font-weight: 500;
}
.images-container {
  padding: 0;
}

#text-box {
  padding-left: 19%;
  padding-right: 19%;
  white-space: nowrap;
}

#psalm19-header {
  font-family: "Nanum Myeongjo", serif;
  text-align: start;
  font-size: 2vw;
  font-weight: 700;
}

.alone-text {
  position: absolute;
  top: 15%;
  left: 10%;
  color: #663116;
  font-family: "Amiri", serif;
  font-size: 1.75vw;
  text-align: start;
}
.images-container img {
  width: 100%;
}
html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
}
